import React, { useEffect, useState } from "react";
import LogoUrl from "../assets/logo-figma.svg";

export default function ImageComp(props) {

      const { src, style, ...rest } = props;
      const [source, setSource] = useState(LogoUrl);
      const [newStyle, setNewStyle] = useState(style ?? {});
      
      const getImage = () => {
            const image = new Image();

            const initialPadding = newStyle.padding;

            image.onload = () => {
                  setSource(src);
                  setNewStyle({ ...newStyle, padding: "0px" });
            }
            image.src = src;

            setNewStyle({ ...newStyle, padding: "10px" });
      }
      // getImage();
      useEffect(() => {
            getImage();
      }, [props]);

      return (
            React.createElement("img", { ...rest, src: source, style: newStyle })
      );
}